.myRoutineForm {
    display: flex;
    flex-direction: column;
    width: 400px;
    margin-top: 10px;
    padding: 5px;
    border: 1px solid gray;
    border-radius: 5px;
    text-align: center;
    background-color:lightgray;
}

.myRoutinesDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.userRoutines {
    display: flex;
    flex-wrap: wrap;
    margin-left: 50px;
    margin-top: 10px;
}

.userRoutineCard {
    padding: 5px;
    margin: 5px;
}
